.AppBackground {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  height: 100vh;
  width: 100vw;
  padding-bottom: 50px;
  padding-top: 50px;

  .bg-content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 80%;
    height: 100%;
  }

  .AppBackground-Content {
    min-width: 100%;
    max-width: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 30% 60%;
    column-gap: 10%;
    margin-top: 30px;
    .Background-tabs {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      max-width: 100%;
      width: 100%;
      min-width: 100%;
      transition: ease-in-out 0.5s;
      .tb {
        padding: 16px;
        cursor: pointer;
        width: 100%;
        border-radius: 10px;
        margin-left: 10px;
      }
    }
    .Background-tab_Section {
      max-width: 100%;
      min-width: 100%;
      transition: ease-in-out 0.5s;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      .Tab-Content {
        min-width: 100%;
        transition: ease-in-out 0.5s;
        .Tab-Content-item {
          margin-bottom: 20px;
          padding: 20px;
          transition: ease-in-out 0.5s;
          display: flex;
          flex-direction: column;
          gap: 4px;
          .where-link {
            margin-top: 5px;
          }
          .Tab-Content-item_when {
            display: block;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 595px) {
  .AppBackground {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    height: 100vh;
    width: 100vw;
    padding-bottom: 50px;
    padding-top: 50px;

    .bg-content {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }

    .AppBackground-Content {
      min-width: 100%;
      display: flex;
      flex-direction: column;
      gap: 5%;
      margin-top: 30px;
      .scroll-container {
        overflow-x: scroll;
      }
      .Background-tabs {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        max-width: 100%;
        width: 100%;
        min-width: 100%;
        min-height: 5vh;
        transition: ease-in-out 0.5s;
        .tb {
          padding: 16px;
          cursor: pointer;
          width: fit-content;
          border-bottom: 0px solid #646c93;
          margin-left: 4px;
        }
      }
      .Background-tab_Section {
        max-width: 100%;
        min-width: 100%;
        transition: ease-in-out 0.5s;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin-top: 20px;
        .Tab-Content {
          min-width: 100%;
          transition: ease-in-out 0.5s;
          .Tab-Content-item {
            margin-bottom: 20px;
            padding: 20px;
            transition: ease-in-out 0.5s;
            display: flex;
