.App-Home {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: start;
  padding-bottom: 50px;
  padding-top: 50px;
}

.AppHome-Content {
  height: 100%;
  margin-top: 20vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  .AppHome-Content_layer-1 {
    max-width: 50%;
  }
  .AppHome-Content_layer-2 {
    height: 100%;
  }
  .acc-row_left-img {
    filter: grayscale(100);
    transition: ease-in-out 0.5s;
    z-index: 0;
    padding: 8px;
    border-radius: 50%;
  }
  .img-container {
    height: 100%;
    border-radius: 50%;
  }
}

@media only screen and (max-width: 595px) {
  .App-Home {
    padding-bottom: 10px;
    .AppHome-Content {
      gap: 30px;
      width: 100%;
    }
  }

  .AppHome-Content_layer-1 {
    max-width: 100%;
    min-width: 100%;
    min-height: 60vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .AppHome-Content_layer-2 {
    height: fit-content;

    .img-container {
      height: 100%;
      border-radius: 0%;
      margin: 4px auto;
      .acc-row_left-img {
        padding: 8px;
        border-radius: 0%;
        width: 400px;
        height: 400px !important;
      }
    }
  }
}

@media only screen and (min-width: 596px) and (max-width: 1099px) {
  .App-Home {
    padding-bottom: 50px;
    .AppHome-Content {
      gap: 30px;
      width: 100%;
    }
  }

  .AppHome-Content_layer-1 {
    max-width: 100%;
    min-width: 100%;
    min-height: 60vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .content-desc {
      max-width: 80%;
    }
  }
  .AppHome-Content_layer-2 {
    height: fit-content;
  }
}

@media only screen and (min-width: 1100px) and (max-width: 1280px) {
  .App-Home {
    padding-bottom: 50px;
    .AppHome-Content {
      gap: 10px;
      width: 100%;
      display: grid;
      grid-template-columns: 60% 35%;
    }
  }

  .AppHome-Content_layer-1 {
    max-width: 100%;
    min-width: 100%;
    min-height: 60vh;
    text-align: start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;

    .content-desc {
      max-width: 80%;
    }
  }
  .AppHome-Content_layer-2 {
    height: 600px;
    .img-container {
      height: 400px;
      border-radius: 16px;
      margin: 4px auto;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      .acc-row_left-img {
        padding: 8px;
        border-radius: 0%;
        width: 100%;
        height: 400px !important;
      }
    }
  }
}
