.portfolio-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  width: 100vw;
  min-height: 100vh;
  .portfolio-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    width: 100%;
    min-width: 100%;
    .pcc-headings {
    }
    .pcc-body {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px;
      width: 100%;
      min-width: 100%;
      gap: 10px;
      .portfolio-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 70vh;
        height: 70vh;
        .portfolio-item_layer2 {
          text-align: start;
          display: flex;
          flex-direction: column;
          justify-content: start;
          height: 60%;
          min-height: 60%;
          max-height: 60%;
          .project-tools {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            overflow: hidden;
            display: none;
            gap: 10px;
            padding: 4%;
            .portfolio-tools_item {
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              width: fit-content;
              padding: 4px;
              margin-right: 6px;
              border-radius: 0.5rem;
            }
          }
          .project-details {
            padding: 4%;
          }
          .livelinks {
            display: flex;
            flex-direction: row;
            justify-content: start;
            align-items: center;
            margin-top: 40px;
            min-height: 3vh;
            padding: 4%;
            .livelinks-item {
              margin-right: 16px;
              &:hover {
                border: 1px solid white;
                padding: 6px 6px;
                transition: ease-in-out 0.5s;
                border-radius: 0.3rem;
              }
              .live_link {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 4px;
              }
            }
          }
        }

        .portfolio-item_layer1 {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          min-width: 100%;
          min-height: 40%;
          max-height: 40%;
          height: 40%;
          padding: 30px auto;
        }
      }
    }
  }
}

