.contact-page {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
}

.contact-content-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 90vw;
}

.ccc-left {
  width: 30%;
  box-sizing: border-box;
  padding: 40px;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
}

.ccc-right {
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 0px 0px 0px;
  margin: 0px auto;
}

.form-holder {
  width: 100%;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0px auto;
  padding: 0px auto;
}
.grey-area {
  max-width: 100%;
  width: 380px;
  margin: 8px 8px 8px 8px;
  border-radius: 16px;
  padding: 10px 10px 10px 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-factory {
  text-align: start;
  width: 100%;
  padding: 0px;
}

.form-elements {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}
textarea {
  border: none;
  padding: 8px;
  margin: 0px auto;
  resize: none;
  background: none;
  outline-width: 0px;
  outline: none;
  border-radius: 8px;
  min-width: 10%;
  box-sizing: border-box;
  width: 100%;
}
label {
  font-size: 14px;
  margin-bottom: 4px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 0px auto;
  text-align: start;
  width: 100%;
}
input[type="text"] {
  min-width: 10%;
  height: 4%;
  min-height: 40px;
  padding: 6px;
  box-sizing: border-box;
  margin: 0px auto;
  border: none;
  border-radius: 8px;
  outline-width: 0px;
  outline: none;
  width: 100%;
}
input:focus {
  outline-width: 0;
  outline: none;
}
.cf-button_row {
  width: 100%;
  margin: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.btn-send {
  width: fit-content;
  background: none;
  border: none;
  font-weight: 900;
  margin-left: 40%;
  cursor: pointer;
}

@media only screen and (max-width: 595px) {
  .contact-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 100vw;
  }
  .contact-page {
    padding-bottom: 50px;
    padding-top: 50px;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  .ccc-left {
    width: 100%;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 10px;
    .tx-ag {
      text-align: center;
      max-width: 80%;
    }
  }

  .ccc-right {
    width: 90%;
    max-width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px;
    padding: 0px;
  }
  .form-holder {
    width: 95%;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .grey-area {
    max-width: 95%;
    min-width: 95%;
  }

  .contact-form {
    max-width: 98%;
    min-width: 98%;
  }
}

@media only screen and (min-width: 596px) and (max-width: 888px) {
  .contact-content-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 100%;
  }
  .contact-page {
    padding-bottom: 50px;
    padding-top: 50px;
  }
  .ccc-left {
    width: 40%;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    row-gap: 10px;
    .tx-ag {
      max-width: 100%;
      font-size: 50px !important;
    }
  }
  .contact-page {
    align-items: center;
  }
}

@media only screen and (min-width: 889px) and (max-width: 1224px) {
  .contact-content-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 100%;
  }
  .contact-page {
    padding-bottom: 50px;
    padding-top: 50px;
  }
  .ccc-left {
    width: 40%;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    row-gap: 10px;
    .tx-ag {
      max-width: 100%;
      font-size: 50px !important;
    }
  }
  .contact-page {
    align-items: center;
  }
}
